const actions = {
  getSizes: (page, search, filter) => ({
    type: 'GET_SIZES_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createSize: () => ({
    type: 'CREATE_SIZE_REQUEST'
  }),
  storeSize: data => ({
    type: 'STORE_SIZE_REQUEST',
    payload: { body: data }
  }),
  editSize: id_size => ({
    type: 'EDIT_SIZE_REQUEST',
    payload: { id_size: id_size }
  }),
  updateSize: (id_size, data) => ({
    type: 'UPDATE_SIZE_REQUEST',
    payload: {
      id_size: id_size,
      body: data
    }
  }),
  showSize: id_size => ({
    type: 'SHOW_SIZE_REQUEST',
    payload: { id_size: id_size }
  }),
  deactivateSize: id_size => ({
    type: 'DEACTIVATE_SIZE_REQUEST',
    payload: { id_size }
  }),
  activateSize: id_size => ({
    type: 'ACTIVATE_SIZE_REQUEST',
    payload: { id_size }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
